import { useEffect } from "react";
import M3U8FileParser from'm3u8-file-parser';

function Tamil() {
    useEffect(() => {
        getData();
    }, []);

    async function getData() {
        try {
            const allResponses = await Promise.all([
                fetch('https://prasanth-snk.github.io/iptv/tamilLocal.m3u').then(resp => resp.text()),
                fetch('https://prasanth-snk.github.io/youtube_m3u/tamil.m3u').then(resp_1 => resp_1.text()),
                fetch('https://iptv-org.github.io/iptv/languages/tam.m3u').then(resp_2 => resp_2.text()),
                fetch('https://prasanth-snk.github.io/iptv/tamil_satellite_tv.m3u').then(resp_3 => resp_3.text())
            ]);
            console.log('allResponses', allResponses)
            return allResponses;
        } catch (err) {
            return err;
        }
    }

    return (
        <div >
            <header >
                Tamil
            </header>
        </div>
    );
}

export default Tamil;
